.home-wrapper {
  @include flex(row);
  height: 100vh;
  background-size: 150px;

  h2 {
    @media (max-width: 768px) {
      font-size: 1.5rem;
    }
    @media (max-width: 425px) {
      font-size: 1rem;
    }
  }

  @media (max-width: $mobileNavBar) {
    margin-top: -3rem;
  }

  .home-logo {
    // @include bg-img("../../assets/images/freelancing99.png", 300, 300);
    margin: 2.4rem auto;
    // border-radius: 50%;
    // box-shadow: 0 0 8px -2px black;
    transition: 0.4s;
    &:hover {
      transform: scale(0.6) rotate(180deg);
      cursor: grab;
    }
  }

  .home-wall-logo {
    width: 128px;
    height: 128px;
    position: absolute;
    top: 0;
    right: 0;
    // box-shadow: 0 0 8px -2px black;
    border-radius: 50%;



    @media (max-width: 545px) {
      width: 80px;
      height: 80px;
    }
    @media (max-width: 320px) {
      display: none;
    }
  
  }
}

.job-role {
  font-family: Arial, Helvetica, sans-serif;
  color: black;
  border-right: 7px solid black;
  border-left: 7px solid black;
  background-color: rgba(216, 85, 38, 0.756);
  padding: 1rem 0;
  text-decoration: none;
  position: relative;

  &::before {
     content: '';
     position: absolute;
     width: 10px;
     height: 10px;
     border-radius: 50%;
     background-color: black;
     left: 5%;
     top: 50%;
     transform: translateY(-50%);
  }
  &::after {
    @extend ::before;
    left: unset;
    right: 5%;
  }
}